import React from 'react';
import smart from '../assets/img/logoSmartFinal.png';
import './estilos.css'

const MenuNav = () => {
	return (
		<div>
			<nav className='navbar navbar-expand-lg navbar-dark bg-dark'>
				<div className='container-fluid'>
				<a class="navbar-brand" href="#"><img src={smart} class="logoa"/></a>
				<button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
					<span className="navbar-toggler-icon"></span>
				</button>
				<div className="collapse navbar-collapse" id="navbarSupportedContent">
					<ul className="navbar-nav me-auto mb-2 mb-lg-0">
						<li className="nav-item">
							<a className="nav-link active" aria-current="page" href="#">Inicio</a>
						</li>
						<li className="nav-item">
							<a className="nav-link" href="#">Galeria</a>
						</li>
						<li className="nav-item dropdown">
							<a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
								Menu
							</a>
							<ul className="dropdown-menu" aria-labelledby="navbarDropdown">
								<li><a className="dropdown-item" href="https://alvarerichardclass.milaulas.com">Sitio-LMS</a></li>
								<li><a className="dropdown-item" href="http://alvarezrichard7cp.epizy.com">Sitio-CMS</a></li>
								<li><hr className="dropdown-divider"/></li>
								<li><a className="dropdown-item" href="#">Something else here</a></li>
							</ul>
						</li>
						<li className="nav-item">
							<a className="nav-link disabled" href="#" tabindex="-1" aria-disabled="true">Contacto</a>
						</li>
					</ul>
					<form className="d-flex">
						<input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
						<button className="btn btn-outline-success btn-secondary text-light border" type="submit">Search</button>
					</form>
				</div>
			</div>
		</nav>		
			
		</div>
	)
}

export default MenuNav 